.page {
  display: grid;
  grid-template-rows: 20px 1fr 20px;
  align-items: center;
  justify-items: center;
  min-height: 100svh;
}


.footer {
  grid-row-start: 3;
  display: flex;
  background: linear-gradient(0deg, #391D1E, #9F5255);
  width: 100%;
  padding: 40px;
  justify-content: center;
  color: #FFF7EF;
}

